import { ReactComponent as BusIcon } from "../asset/Icon/solar_bus-bold.svg";
import { ReactComponent as HSRIcon } from "../asset/Icon/ri_train.svg";
import { ReactComponent as MRTIcon } from "../asset/Icon/MRT.svg";
import classes from "./Traffic.module.css";

const Traffic = () => {
  return (
    <>
      <div className={classes.container}>
        <div className={classes.traffic}>
          <div className={classes["traffic-container"]}>
            <BusIcon width={32} height={32} />
            <div className={classes.content}>
              <span></span>
              <div className={classes.square}></div>
              <span>2122168</span>
            </div>
          </div>
          <div className={classes["traffic-container"]}>
            <MRTIcon width={32} height={32} />
            <div className={classes.content}>
              <span></span>
              <div className={classes.square}></div>
              <span>10</span>
            </div>
          </div>
        </div>
        <div className={classes.traffic}>
          <div className={classes["traffic-container"]}>
            <HSRIcon width={32} height={32} />
            <div className={classes.content}>
              <span></span>
              <div className={classes.square}></div>
              <span></span>
            </div>
          </div>
          <div className={classes["traffic-container"]}>
            <MRTIcon width={32} height={32} />
            <div className={classes.content}>
              <span></span>
              <div className={classes.square}></div>
              <span>5</span>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Traffic;
